.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}


.home-page-container {
  min-height: 250vh;
  width: 100%;
  position: relative;
}

.sb-title {
 position: sticky;
 top: 50px;  
 width: 90%;
 text-align: center;
 margin:auto;
 margin-top: 40vh;
}

.sb-title h1 {
  color: #fff;
  font-size:4vw;
}

.your-sat {
  position: sticky;
  top: 120px;  
  width: 90%;
  text-align: center;
  margin:auto;
  margin-top: 60vh;
}

.your-sat h2 {
  color: #FFB800;
  font-size:4vw;
}

.get-started {
  position: sticky;
  top: 240px;  
  width: 90%;
  text-align: center;
  margin:auto;
  margin-top: 90vh;
}

.get-started h3{
  color: white;
  font-size:3vw;
  font-weight: 600;
}

.get-started h2{
  color: white;
  padding-top: 20px;
  font-size:3vw;
  font-weight: 800;
  cursor: pointer;
}

.question-card {
  margin: 0 auto;

  width: 70%; 
  min-height: 70vh;

  background-color: black;
  padding: 16px;
  border-radius: 22px;
  color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.question-card h3 {
  font-weight: 400;
}

.question-card ul {
  list-style: none;
  font-weight: 800;
  padding: 16px;
  width: 30%;
}

.question-card li {
  margin-top: 16px;
  background-color: white;
  color: black;
  padding: 8px;
  border-radius: 20px;
  transition-duration: 0.2s;
  cursor: pointer;
}

.question-card li:hover {
  background-color: #FFB800;
}

.timer {
  color: white;
  font-weight: 600;
  text-align:right;
  padding-right: 4%;
}

.questionNum {
  text-align:left;
  padding-left: 16px;
}

.final-results {
  margin: 0 auto;

  width: 70%;
  height: 70vh;

  background-color: black;
  padding: 16px;
  border-radius: 22px;
  color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.final-results h2 {
  font-weight: 600;
}

.final-results ul {
  list-style: none;
  font-weight: 800;
  padding: 16px;
  text-align: left;
}

.final-results li {
  margin-top: 8px;
  padding-left: 30%;
}

.final-results span {
  float: right;
  text-align: left;
  padding-right: 40%;
}

.final-results button {
  font-family: 'Poppins', sans-serif;
  font-weight: 800;
  font-size: large;
  margin-bottom: 16px;
  background-color: white;
  color: black;
  padding: 8px;
  border-radius: 10px;
  transition-duration: 0.2s;
  cursor: pointer;
}

.final-results button:hover {
  background-color: #FFB800;
}

.react-switch-checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
}

.react-switch-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 70px;
  height: 30px;
  background: grey;
  border-radius: 100px;
  position: relative;
  transition: background-color .2s;
}

.react-switch-label .react-switch-button {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 25px;
  height: 25px;
  border-radius: 25px;
  transition: 0.2s;
  background: #fff;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}

.react-switch-checkbox:checked + .react-switch-label .react-switch-button {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}

.react-switch-label:active .react-switch-button {
  width: 60px;
}

.review-card {
  display: flex;
}

.review-question-card {

  width: 40%;

  margin-right: 16px;

  background-color: black;
  padding: 16px;
  border-radius: 22px;
  color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.review-question-card h3 {
  font-weight: 400;
}

.review-question-card ul {
  list-style: none;
  font-weight: 800;
  padding: 16px;
  width: 30%;
}

.review-question-card li {
  margin-top: 16px;
  background-color: white;
  color: black;
  padding: 8px;
  border-radius: 20px;
}

.review-questionNum {
  text-align:left;
  padding-left: 16px;
}

.checkandnext {
  position: absolute;
  bottom: 20px;
  text-align: left;
}

.checkandnext input{
  margin-right: 15px;
  width:20px;
  height:20px;
  background:gray;
  border-radius:5px;
  border:2px solid #555;
  vertical-align: middle;
  accent-color: #FFB800;
}

.finish-review {
  margin: 0;
  padding: 0;

  position: absolute;
  top: 0;
  left: 0;
  
  width: 100%;
  height: 100vh;

  background-color: black;
}

.finish-review h1 {
  font-weight: 800;
  color: white;
}

.finish-review h2 {
